import * as React from "react";
import { Button, Table } from 'reactstrap';

// load the Actions and Stores for the elastic search client
const testActions = require('./actions/testActions.js');
const testStore = require('./stores/testStores.js');

interface HelloProps { 
    title: string; 
    framework: string; 
}
  
interface IMyComponentState {
    value : string;
}

class Home extends React.Component<HelloProps, IMyComponentState> {

    state: IMyComponentState;

    constructor(props: HelloProps){
        super(props);
        this.state = {
            value: 'cmon!'
        };
    }

//React life cycle methods below:

    // handleChange(event) {
    //     this.setState({id: event.target.value});
    // }

    // getInitialState(){
    //    this.state = testStore.getState();
    //    return this.state;
    // }

    loadIndex(){
        console.log("loading...");
        testActions.postSearch();
    }

    componentWillMount(){
        testStore.listen(this.onChange);
        //var indexInfo = this.state.indexData;
        //console.log("index info: " + this.state.indexInfo);
    }

    componentDidMount() {
        testStore.listen(this.onChange);
    }

    componentWillUnmount(){
        testStore.unlisten(this.onChange);
    }

    onChange(){
         this.setState(this.state);
    }

/**************************************************/

    render() {
        const list = this.state.value;
        return (
          <div id="serverList">
              <div id="consoleMsg" className="fullSize" style={{width:'55%', height:'40%'}}/>
              <div id="toolbar">
                  <Button color="primary" onClick={this.loadIndex}>Load</Button>
                  <Table>
                    <thead>
                    <tr>
                        <th>#</th>
                        <th>First Name</th>
                        <th>Last Name</th>
                        <th>Username</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <th scope="row">1</th>
                        <td>Mark</td>
                        <td>Otto</td>
                        <td>@mdo</td>
                    </tr>
                    <tr>
                        <th scope="row">2</th>
                        <td>Jacob</td>
                        <td>Thornton</td>
                        <td>@fat</td>
                    </tr>
                    <tr>
                        <th scope="row">3</th>
                        <td>Larry</td>
                        <td>the Bird</td>
                        <td>@twitter</td>
                    </tr>
                    <tr>
                        <th scope="row">4</th>
                        <td>{this.state}</td>
                        <td>the Bird</td>
                        <td>@twitter</td>
                    </tr>
                    </tbody>
                  </Table>
              </div>
          </div>
        );
    }
}

export default Home